<template>
    <v-container class="pa-0">
        <!-- Header -->
        <PDHeader></PDHeader>

        <!-- Main -->
        <v-main class="py-5 main-container">
            <!-- 月份顯示 -->
            <v-container class="pt-0">
                <v-row justify="center">
                    <v-col cols="12" class="d-flex justify-center">

                        <div class="month-label">
                            <v-btn icon="mdi-chevron-left" class="month-btn mx-5" @click="minusMonth"></v-btn>
                            <span>{{ dayjs(displayDate).format("YYYY-MM") }}</span>
                            <v-btn icon="mdi-chevron-right" class="month-btn mx-5" @click="plusMonth"></v-btn>
                        </div>

                    </v-col>

                </v-row>
            </v-container>
            <v-divider class="mb-5" thickness="2"></v-divider>
            <!-- 紀錄列表(每日) -->
            <v-container class="pa-0 ma-0" v-if="!loading">
                <v-card class="mx-10 mb-5 card" elevation="10" v-for="item in recordList" :key="item.Date"
                    @click="showdialog(item)">
                    <v-card-item class="card-title">
                        <v-card-title>
                            {{ dayjs(item.date).format("MM-DD") }}
                        </v-card-title>
                    </v-card-item>

                    <v-card-text class="py-0">
                        <v-row align="center" no-gutters class="mt-5 mb-10">
                            <v-col class="text-h5 d-flex align-center" cols="6">
                                <v-icon icon="mdi-weight-kilogram"></v-icon>
                                <span>{{ item.weight }}</span>
                            </v-col>

                            <v-col cols="6" class="text-h5 d-flex align-center">
                                <v-icon color="error" icon="mdi-blood-bag"></v-icon>
                                <span>{{ item.blood_pressure_high }}/{{ item.blood_pressure_low }}</span>
                            </v-col>
                        </v-row>

                        <v-row align="center" no-gutters class="mb-5">
                            <v-col class="text-h5 d-flex align-center" cols="6">
                                <v-icon icon="mdi-water-remove" color="blue"></v-icon>
                                <span>{{ item.total_dehydration }}</span>
                                <!-- <span>全日脫水量:</span> -->
                            </v-col>

                            <v-col class="text-h5 d-flex align-center" cols="6">
                                <v-icon icon="mdi-cube-outline"></v-icon>
                                <span>{{ item.blood_glucose_morning }}</span>
                            </v-col>
                        </v-row>
                    </v-card-text>

                </v-card>
            </v-container>
            <!-- loading動畫 -->
            <v-dialog fullscreen v-model="loading">
                <v-progress-circular color="#2196f3" indeterminate
                    style="position: absolute; top:50%;left:50%;transform:translate(-50%,-50%)" :size="58"
                    :width="10"></v-progress-circular>
            </v-dialog>

            <!-- 點擊紀錄跳出燈箱 -->
            <v-dialog v-model="show" fullscreen>
                <!-- CAPD類紀錄 -->
                <v-card v-if="type === 'CAPD'">
                    <v-card-title>
                        {{ dayjs(selectedData.date).format("YYYY-MM-DD") }}
                    </v-card-title>
                    <v-card-text class="pa-0">
                        <v-container>

                            <v-row>
                                <v-col cols="12" class="d-flex align-center">
                                    <span style="width: 30%;">血壓&nbsp;:</span>

                                    <v-row class="d-flex align-center">
                                        <v-col cols="5">
                                            <v-text-field type="number" variant="outlined" color="blue"
                                                v-model="selectedData.blood_pressure_high" />
                                        </v-col>
                                        <v-col cols="1"><span>/</span></v-col>
                                        <v-col cols="5">
                                            <v-text-field type="number" variant="outlined" color="blue"
                                                v-model="selectedData.blood_pressure_low" />
                                        </v-col>
                                    </v-row>

                                </v-col>
                            </v-row>

                            <v-row>
                                <v-col>
                                    <span>體重&nbsp;:</span><v-text-field type="number" variant="outlined" color="blue"
                                        v-model="selectedData.weight" />
                                </v-col>
                            </v-row>

                            <v-row>
                                <v-col>
                                    <span style="width: 30%;">全日脫水量:&nbsp;</span><v-text-field type="number"
                                        variant="outlined" readonly color="blue" v-model="selectedData.total_dehydration" />
                                </v-col>
                            </v-row>

                            <v-row>
                                <v-col cols="12">透析液:注入量/排出量</v-col>

                                <template v-for="(item, index) in v_for_array" :key="index">

                                    <v-col cols="12" class="d-flex justify-space-between align-center pb-0">
                                        <span style="width: 30%;">第{{ item }}次:&nbsp;</span>
                                        <v-text-field type="number" variant="underlined" color="blue"
                                            v-model="selectedData[`injection${item}`]" /><span>&nbsp;/&nbsp;</span>
                                        <v-text-field type="number" variant="underlined" color="blue"
                                            v-model="selectedData[`dehydration${item}`]" />
                                    </v-col>
                                    <v-col class="pt-0">
                                        <v-select :items="concentration_list" v-model="selectedData[`concentration_name_${item}`]" label="濃度"></v-select>
                                        <v-select :items="drainage_time_list" v-model="selectedData[`drainage_time_name_${item}`]" label="引流時間"></v-select>
                                        <v-select :items="CAPD_dehydration_state_list" v-model="selectedData[`state_name_${item}`]" label="排出藥水狀態"></v-select>
                                    </v-col>
                                </template>

                            </v-row>

 
                            <v-row>
                                <v-col cols="12">血糖濃度</v-col>
                                <v-col cols="12"> <span>早:&nbsp;</span><v-text-field type="number" variant="outlined"
                                        color="blue" v-model="selectedData.blood_glucose_morning" /></v-col>
                                <v-col cols="12"> <span>中:&nbsp;</span><v-text-field type="number" variant="outlined"
                                        color="blue" v-model="selectedData.blood_glucose_afternoon" /></v-col>
                                <v-col cols="12"> <span>晚:&nbsp;</span><v-text-field type="number" variant="outlined"
                                        color="blue" v-model="selectedData.blood_glucose_night" /></v-col>
                            </v-row>

                            <v-btn icon="mdi-close-thick" class="close-btn" @click="show = false"></v-btn>
                        </v-container>
                    </v-card-text>
                </v-card>
                <!-- APD類紀錄 -->
                <v-card v-else-if="type === 'APD'">
                    <v-card-title>
                        {{ dayjs(selectedData.date).format("YYYY-MM-DD") }}
                    </v-card-title>
                    <v-card-text class="pa-0">
                        <v-container>

                            <v-row>
                                <v-col cols="12" class="d-flex align-center">
                                    <span style="width: 30%;">血壓&nbsp;:</span>
                                    <v-row class="d-flex align-center">
                                        <v-col cols="5"><v-text-field type="number" variant="outlined" color="blue"
                                                v-model="selectedData.blood_pressure_high" /></v-col>
                                        <v-col cols="1"><span>/</span></v-col>
                                        <v-col cols="5"><v-text-field type="number" variant="outlined" color="blue"
                                                v-model="selectedData.blood_pressure_low" /></v-col>
                                    </v-row>

                                </v-col>

                            </v-row>

                            <v-row>
                                <v-col>
                                    <span>體重&nbsp;:</span><v-text-field type="number" variant="outlined" color="blue"
                                        v-model="selectedData.weight" />
                                </v-col>
                            </v-row>

                            <v-row>
                                <v-col>
                                    <span>0週期引流量&nbsp;:</span><v-text-field type="number" variant="outlined" color="blue"
                                        v-model="selectedData.zero_drainage" />
                                </v-col>
                            </v-row>

                            <v-row>
                                <v-col>
                                    <span>最末袋注入量&nbsp;:</span><v-text-field type="number" variant="outlined" color="blue"
                                        v-model="selectedData.last_injection" />
                                </v-col>
                            </v-row>

                            <v-row>
                                <v-col>
                                    <span>APD顯示總脫水量&nbsp;:</span><v-text-field type="number" variant="outlined" color="blue"
                                        v-model="selectedData.APD_dehydration" />
                                </v-col>
                            </v-row>


                            <v-row class="d-flex align-center">
                                <v-col cols="6">
                                    <span>CAPD引流量&nbsp;:</span><v-text-field type="number" variant="outlined" color="blue"
                                        v-model="selectedData.CAPD_drainage" />
                                </v-col>
                                <v-col cols="6">
                                    <span>CAPD注入量&nbsp;:</span><v-text-field type="number" variant="outlined" color="blue"
                                        v-model="selectedData.CAPD_injection" />
                                </v-col>
                            </v-row>

                            <v-row class="d-flex align-center">
                                <v-col>
                                    <span>CAPD脫水量&nbsp;:</span><v-text-field type="number" variant="outlined" color="blue"
                                        v-model="selectedData.CAPD_dehydration" />
                                </v-col>
                            </v-row>

                            <v-row class="d-flex align-center">
                                <v-col>
                                    <span>全天總脫水量&nbsp;:</span><v-text-field type="number" variant="outlined" readonly
                                        color="blue" v-model="selectedData.total_dehydration" />
                                </v-col>
                            </v-row>

                            <v-row class="d-flex align-center">
                                <v-col>
                                    <span>平均留置時間(小時:分鐘)&nbsp;:</span><v-text-field type="text" variant="outlined" readonly
                                        color="blue" v-model="selectedData.average_time" />
                                </v-col>
                            </v-row>

                            <v-row>
                                <v-col cols="12">血糖濃度</v-col>
                                <v-col cols="12"> <span>早:&nbsp;</span><v-text-field type="number" variant="outlined"
                                        color="blue" v-model="selectedData.blood_glucose_morning" /></v-col>
                                <v-col cols="12"> <span>中:&nbsp;</span><v-text-field type="number" variant="outlined"
                                        color="blue" v-model="selectedData.blood_glucose_afternoon" /></v-col>
                                <v-col cols="12"> <span>晚:&nbsp;</span><v-text-field type="number" variant="outlined"
                                        color="blue" v-model="selectedData.blood_glucose_night" /></v-col>
                            </v-row>

                            <v-row>
                                <v-col>
                                    <v-select :items="heating_bag_list" v-model="selectedData.heating_bag_name"
                                        label="加溫袋"></v-select>
                                    <v-select :items="refill_bag_list" v-model="selectedData.refill_bag_name"
                                        label="補充袋"></v-select>
                                    <v-select :items="last_bag_list" v-model="selectedData.last_bag_name" label="最末袋"></v-select>
                                    <v-select :items="APD_dehydration_state_list" v-model="selectedData.state_name"
                                        label="排出藥水狀態"></v-select>
                                    <v-select :items="machine_warning_list" v-model="selectedData.machine_warning_name"
                                        label="機器警示次數"></v-select>
                                </v-col>
                            </v-row>

                            <v-row>
                                <v-col cols="12">
                                    <v-textarea label="備註"></v-textarea>
                                </v-col>
                            </v-row>


                            <v-btn icon="mdi-close-thick" class="close-btn" @click="show = false"></v-btn>
                        </v-container>
                    </v-card-text>


                </v-card>

                <v-btn icon="mdi-trash-can" class="delete-btn" @click="deletedata"></v-btn>
            </v-dialog>

            <!-- 增加藥水按鈕 -->
            <router-link to="/addperitonealdialysis"><v-btn rounded="circle" icon="mdi-plus-thick"
                    class="add-btn"></v-btn></router-link>

        </v-main>

        <!-- Footer -->
        <PDFooter></PDFooter>
    </v-container>
</template>

<script setup>
// components
import PDHeader from '@/components/PD-Header.vue';
import PDFooter from '@/components/PD-Footer.vue'
import { ref, computed, onMounted } from 'vue';//eslint-disable-line
import dayjs from 'dayjs';
import axios from 'axios';

const loading = ref(true)
const show = ref(false);
onMounted(async () => {
    await get_peritoneal_data();
    const peritonealSectionResponse = await axios.get("/api/Peritoneal/get_peritoneal_section")
    section_list.value = peritonealSectionResponse.data;
    concentration_list.value = peritonealSectionResponse.data.filter(item => {
        return item.sectype == 'concentration'
    }).map(item => {
        return item.secname;
    });
    drainage_time_list.value = peritonealSectionResponse.data.filter(item => {
        return item.sectype == 'drainage_time'
    }).map(item => {
        return item.secname;
    });
    CAPD_dehydration_state_list.value = peritonealSectionResponse.data.filter(item => {
        return item.sectype == 'CAPD_dehydration_state'
    }).map(item => {
        return item.secname;
    });
    heating_bag_list.value = peritonealSectionResponse.data.filter(item => {
        return item.sectype == 'heating_bag'
    }).map(item => {
        return item.secname;
    });
    refill_bag_list.value = peritonealSectionResponse.data.filter(item => {
        return item.sectype == 'refill_bag'
    }).map(item => {
        return item.secname;
    });
    last_bag_list.value = peritonealSectionResponse.data.filter(item => {
        return item.sectype == 'last_bag'
    }).map(item => {
        return item.secname;
    });
    machine_warning_list.value = peritonealSectionResponse.data.filter(item => {
        return item.sectype == 'machine_warning'
    }).map(item => {
        return item.secname;
    });
    APD_dehydration_state_list.value = peritonealSectionResponse.data.filter(item => {
        return item.sectype == 'APD_dehydration_state'
    }).map(item => {
        return item.secname;
    });

})
// data
//顯示月份
const displayDate = ref(dayjs());
//燈箱顯示類型
const type = ref('');
//選中資料
const selectedData = ref(null);
//透析紀錄資料
const recordList = ref([]);
//選項設定清單
const section_list = ref();
//濃度選項
const concentration_list = ref()
//平均時間選項
const drainage_time_list = ref()
//CAPD狀態選項
const CAPD_dehydration_state_list = ref()
//APD狀態選項
const APD_dehydration_state_list = ref()
//加溫袋選項
const heating_bag_list = ref()
// 補充袋選項
const refill_bag_list = ref()
//最末袋選項
const last_bag_list = ref()
//機器警示次數選項
const machine_warning_list = ref()
//v-for遍歷陣  !!需要使用此陣列進行v-for渲染再以v_for_array[index]來做v-model綁定=>
const v_for_array = [1, 2, 3, 4, 5]
// 月份增減

const plusMonth = () => {
    displayDate.value = displayDate.value.add(1, 'month')
    get_peritoneal_data()
}

const minusMonth = () => {
    displayDate.value = displayDate.value.subtract(1, 'month')
    get_peritoneal_data()
}

// 燈箱
const showdialog = async (item) => {
    type.value = item.type;
    selectedData.value = item;
    show.value = true;
    console.log(selectedData.value);
};

// 刪除資料
const deletedata = () => {//eslint-disable-line
}


const get_peritoneal_data = async () => {
    try {
        loading.value = true;
        const peritonealRespone = await axios.get('/api/Peritoneal/get_peritoneal_data', {
            params: {
                PTSEQ: 1,
                displaymonth: displayDate.value.format("YYYY-MM")
            }
        });
        const peritonealData = await peritonealRespone.data.map(item => {
            if (item.dtype == "APD") {
                return {
                    PTSEQ: item.ptseq,
                    date: item.rdte,
                    type: item.dtype,
                    blood_pressure_high: item.bph,
                    blood_pressure_low: item.bpl,
                    weight: item.weight,
                    total_dehydration: item.tdeh,
                    blood_glucose_morning: item.mglu,
                    blood_glucose_afternoon: item.aglu,
                    blood_glucose_night: item.nglu,
                    CAPD_drainage: item.capddra,
                    CAPD_injection: item.capdinj,
                    CAPD_dehydration: item.capddeh,
                    heating_bag_ID: item.hbagid,
                    heating_bag_name: item.hbagname,
                    refill_bag_ID: item.rbagid,
                    refill_bag_name: item.rbagname,
                    last_bag_ID: item.lbagid,
                    last_bag_name: item.lbagname,
                    machine_warning_ID: item.wrid,
                    machine_warning_name: item.wrname,
                    state_ID:item.stateid,
                    state_name:item.statename,
                    zero_drainage: item.zrodra,
                    APD_dehydration: item.apddeh,
                    last_injection: item.lastinj,
                    average_time: item.avgtme,
                    note: item.note
                }
            }
            else if (item.dtype == "CAPD") {
                return {
                    PTSEQ: item.ptseq,
                    date: item.rdte,
                    type: item.dtype,
                    blood_pressure_high: item.bph,
                    blood_pressure_low: item.bpl,
                    weight: item.weight,
                    total_dehydration: item.tdeh,
                    blood_glucose_morning: item.mglu,
                    blood_glucose_afternoon: item.aglu,
                    blood_glucose_night: item.nglu,
                    injection1: item.inJ1,
                    dehydration1: item.deH1,
                    concentration_ID_1: item.dcoN1,
                    concentration_name_1: item.dcoN1NAME,
                    drainage_time_ID_1: item.dtmE1,
                    drainage_time_name_1: item.dtmE1NAME,
                    state_ID_1: item.statE1,
                    state_name_1: item.statE1NAME,
                    injection2: item.inJ2,
                    dehydration2: item.deH2,
                    concentration_ID_2: item.dcoN2,
                    concentration_name_2: item.dcoN2NAME,
                    drainage_time_ID_2: item.dtmE2,
                    drainage_time_name_2: item.dtmE2NAME,
                    state_ID_2: item.statE2,
                    state_name_2: item.statE2NAME,
                    injection3: item.inJ3,
                    dehydration3: item.deH3,
                    concentration_ID_3: item.dcoN3,
                    concentration_name_3: item.dcoN3NAME,
                    drainage_time_ID_3: item.dtmE3,
                    drainage_time_name_3: item.dtmE3NAME,
                    state_ID_3: item.statE3,
                    state_name_3: item.statE3NAME,
                    injection4: item.inJ4,
                    dehydration4: item.deH4,
                    concentration_ID_4: item.dcoN4,
                    concentration_name_4: item.dcoN4NAME,
                    drainage_time_ID_4: item.dtmE4,
                    drainage_time_name_4: item.dtmE4NAME,
                    state_ID_4: item.statE4,
                    state_name_4: item.statE4NAME,
                    injection5: item.inJ5,
                    dehydration5: item.deH5,
                    concentration_ID_5: item.dcoN5,
                    concentration_name_5: item.dcoN5NAME,
                    drainage_time_ID_5: item.dtmE5,
                    drainage_time_name_5: item.dtmE5NAME,
                    state_ID_5: item.statE5,
                    state_name_5: item.statE5NAME
                }
            }

        })
        loading.value = false;
        recordList.value = peritonealData;
    }
    catch (error) {
        console.log(error);
    }

}



</script>

<style lang="scss" scoped>
.main-container {
    position: relative;

    a {
        text-decoration: none;
        position: fixed;
        right: 10px;
        bottom: 70px;

        .add-btn {
            width: 60px;
            height: 60px;
            padding: 0;
            font-size: 24px;
            background-color: rgb(22, 231, 3);
            color: black;
        }



    }

    .month-label {
        font-size: 24px;
        display: flex;
        justify-content: space-around;
        align-items: center;

        .month-btn {
            font-size: 16px;
            background-color: lightgray;
        }
    }

}

.close-btn {
    position: fixed;
    box-shadow: none;
    top: 0;
    right: 0;
    width: 50px;
    height: 50px;
    font-size: 20px;
}

.delete-btn {
    position: fixed;
    bottom: 10px;
    right: 10px;
    width: 50px;
    height: 50px;
    font-size: 20px;
    background-color: red;
}

::v-deep .v-card-title {
    font-size: 28px;
}

.card {
    border-radius: 20px;
    border: 0.5px solid gray;
}
</style>